ansform: scale(1.2, 1.2);\r\n\ttransform: scale(1.2, 1.2);\r\n}\r\n.clockpicker-minutes.clockpicker-dial-out {\r\n\t-webkit-transform: scale(.8, .8);\r\n\t-moz-transform: scale(.8, .8);\r\n\t-ms-transform: scale(.8, .8);\r\n\t-o-transform: scale(.8, .8);\r\n\ttransform: scale(.8, .8);\r\n}\r\n.clockpicker-canvas {\r\n\t-webkit-transition: opacity 175ms;\r\n\t-moz-transition: opacity 175ms;\r\n\t-ms-transition: opacity 175ms;\r\n\t-o-transition: opacity 175ms;\r\n\ttransition: opacity 175ms;\r\n}\r\n.clockpicker-canvas-out {\r\n\topacity: 0.25;\r\n}\r\n.clockpicker-canvas-bearing {\r\n\tstroke: none;\r\n\tfill: $secondary-color;\r\n}\r\n.clockpicker-canvas-bg {\r\n\tstroke: none;\r\n\tfill: $secondary-color;\r\n}\r\n.clockpicker-canvas-bg-trans {\r\n\tfill: $secondary-color;\r\n}\r\n.clockpicker-canvas line {\r\n\tstroke: $secondary-color;\r\n\tstroke-width: 4;\r\n\tstroke-linecap: round;\r\n\t/*shape-rendering: crispEdges;*/\r\n}\r\n",".gray-halftone {\n  position: relative;\n  overflow: hidden;\n\n  & > * {\n    position: relative;\n    z-index: 1;\n  }\n\n  &::before {\n    content: '';\n    background-image: url(./images/gray-halftone.svg);\n    background-position: bottom;\n    background-repeat: repeat-x;\n    position: absolute;\n    bottom: -50px;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    transform: rotate(-4deg) scale(1.05);\n    z-index: 0;\n  }\n}\n\n@mixin slanted($type: 'normal') {\n  $main-clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);\n  $navy-clip-path: polygon(0 calc(100% - 10px), 100% calc(80% - 10px), 100% 80%, 0 100%);\n  $ice-clip-path: polygon(0 calc(100% - 9px), 100% calc(80% - 9px), 100% calc(80% - 1px), 0 calc(100% - 1px));\n\n  @if $type == 'right' {\n    $main-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);\n    $navy-clip-path: polygon(0 calc(80% - 10px), 100% calc(100% - 10px), 100% 100%, 0 80%);\n    $ice-clip-path: polygon(0 calc(80% - 9px), 100% calc(100% - 9px), 100% calc(100% - 1px), 0 calc(80% - 1px));\n  }\n  @if $type == 'shallow' {\n    $main-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 90%);\n    $navy-clip-path: polygon(0 calc(90% - 10px), 100% calc(100% - 10px), 100% 100%, 0 90%);\n    $ice-clip-path: polygon(0 calc(90% - 9px), 100% calc(100% - 9px), 100% calc(100% - 1px), 0 calc(90% - 1px));\n  }\n\n  clip-path: $main-clip-path;\n\n  &::before, &::after {\n    content: '';\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    height: 100%;\n  }\n\n  &::before {\n    background: $navy;\n    clip-path: $navy-clip-path;\n    z-index: 1;\n  }\n\n  &::after {\n    background: $ice;\n    clip-path: $ice-clip-path;\n    z-index: 2;\n  }\n}\n","// Change default container widths\n.container {\n  @media #{$large-and-up} {\n    width: 95%;\n  }\n  @media #{$extra-large-and-up} {\n    width: 90%;\n  }\n}\n\n// Flex grid\n.flex-grid {\n  display: flex;\n  flex-flow: row wrap;\n  justify-content: flex-start;\n\n  .col.col {\n    flex: 0 0 auto;\n    margin-right: 0;\n\n    &:not([class*=\"offset\"]) {\n      margin-left: 0;\n    }\n  }\n\n  .card-panel {\n    height: calc(100% - 1.5em);\n  }\n}\n\n// Block grid\n.block-grid {\n  display: flex;\n  flex-flow: row wrap;\n\n  @for $i from 1 through 6 {\n    &.up-s#{$i} {\n      @media #{$small-and-down} {\n        .col {\n          width: 100% / $i;\n        }\n      }\n    }\n\n    &.up-m#{$i} {\n      @media #{$medium-and-up} {\n        .col {\n          width: 100% / $i;\n        }\n      }\n    }\n\n    &.up-l#{$i} {\n      @media #{$large-and-up} {\n        .col {\n          width: 100% / $i;\n        }\n      }\n    }\n  }\n}\n","html {\n  color: $gray;\n  font-size: 18px;\n  font-family: $font-family-sans-serif;\n}\n\n.uppercase {\n  text-transform: uppercase;\n}\n\nh1 {\n  color: $aqua;\n  font-family: $font-family-serif;\n  font-size: 60px;\n  font-weight: 600;\n}\n\nh2 {\n  color: $green-med;\n  font-family: $font-family-sans-serif;\n  font-size: 46px;\n  font-weight: 600;\n}\n\nh3 {\n  color: $navy;\n  font-family: $font-family-serif;\n  font-size: 30px;\n  font-weight: 600;\n}\n\nh4 {\n  color: $navy;\n  font-family: $font-family-sans-serif;\n  font-size: 18px;\n  font-weight: 600;\n}\n\nh5 {\n  color: $gray;\n  font-family: $font-family-serif;\n  font-size: 18px;\n  font-weight: 600;\n}\n\nh6 {\n  color: $gray;\n  font-family: $font-family-sans-serif;\n  font-size: 18px;\n  font-weight: 600;\n  text-transform: uppercase;\n}\n\n.hentry {\n  ul {\n    list-style-type: disc;\n    padding-left: 2em;\n  }\n\n  li {\n    list-style-type: inherit !important;\n  }\n}\n","//Global button variables\n\n@mixin btn-global {\n  box-shadow: 7px 8px $shadow;\n  color: white;\n  display: inline-block;\n  font-family: $font-comic;\n  font-weight: normal;\n  font-size: 36px;\n  text-transform: uppercase;\n  transform: translate(0, 0);\n  transition: all 0.3s ease;\n  padding: 10px 15px !important;\n  position: relative;\n  border-radius: 0 !important;\n}\n@mixin btn-before-global {\n  content: \" \";\n  position: absolute;\n  z-index: -1;\n  top: -6px;\n  left: -6px;\n  right: -6px;\n  bottom: -6px;\n}\n@mixin btn-hover-global {\n  box-shadow: 2px 4px $shadow;\n  transform: translate(2px, 2px);\n}\n\n// Ultimate member buttons\ninput[type=\"submit\"].um-button {\n  border: 1px solid $aqua-dark !important;\n}\n\n.um-button.um-alt {\n  border: 1px solid $green-dark !important;\n}\n\n//Primary $aqua CTA Button\n\n.btn-primary, input[type=\"submit\"].um-button {\n  @include btn-global;\n\n  background-color: $aqua;\n  border: 6px solid $aqua-light;\n  text-shadow: 2px 4px $aqua-dark;\n\n  &::before {\n    @include btn-before-global;\n\n    border: 2px solid $aqua-dark;\n  }\n\n  &:hover {\n    @include btn-hover-global;\n\n    background-color: $aqua-light;\n    border: 6px solid $aqua;\n  }\n\n  &:active {\n    background-color: $aqua;\n    border: 6px solid $aqua-dark;\n    box-shadow: 0 0;\n  }\n}\n\n//Secondary $green-med CTA button\n\n.btn-secondary, .um-button.um-alt {\n  @include btn-global;\n\n  background-color: $green-med;\n  border: 6px solid $green-light;\n  padding: 2px 40px;\n  text-shadow: 2px 4px $green-dark;\n\n  &::before {\n    @include btn-before-global;\n\n    border: 2px solid $green-dark;\n  }\n\n  &:hover {\n    @include btn-hover-global;\n\n    background-color: $green-light;\n    border: 6px solid $green-med;\n  }\n\n  &:active {\n    background-color: $green-med;\n    border: 6px solid $green-dark;\n    box-shadow: 0 0;\n  }\n}\n\n//Tertiary $orange-med CTA Button\n\n.btn-tertiary {\n  @include btn-global;\n\n  background-color: $orange-med;\n  border: 6px solid $orange-light;\n  font-size: 36px;\n  padding: 2px 20px;\n  text-shadow: 2px 4px $orange-dark;\n\n  &::before {\n    @include btn-before-global;\n\n    border: 2px solid $orange-dark;\n  }\n\n  &:hover {\n    @include btn-hover-global;\n\n    background-color: $orange-light;\n    border: 6px solid $orange-med;\n  }\n\n  &:active {\n    background-color: $orange-med;\n    border: 6px solid $orange-dark;\n    box-shadow: 0 0;\n  }\n}\n","input {\n  font-family: $font-family-serif;\n  color: $aqua-dark;\n}\n\nlabel {\n  @extend h6;\n}\n",".observation.card {\n  box-shadow: 8px 8px $shadow;\n  border-radius: 0;\n  margin: 20px 0;\n  position: relative;\n  color: $navy;\n\n  a.mega-link {\n    display: block;\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: 100;\n  }\n\n  img {\n    border: 2px solid white;\n    margin: 13px !important;\n  }\n\n  &.horizontal {\n    img {\n      max-width: 75px;\n      height: auto;\n    }\n  }\n\n  .card-content {\n    padding: 10px 0;\n  }\n\n  .material-icons {\n    font-size: 18px;\n    vertical-align: top;\n  }\n\n  h3 {\n    font-family: $font-family-sans-serif;\n    font-size: 18px;\n    margin: 5px 0;\n    color: white;\n\n    a {\n      color: white;\n    }\n  }\n\n  p {\n    font-size: 14px;\n  }\n\n  ul {\n    margin: 0;\n    padding: 0;\n  }\n\n  li {\n    font-size: 12px;\n    display: inline-block;\n\n    &:first-child {\n      padding-right: 20px;\n    }\n  }\n\n  // 1, 5, 9, etc\n  &:nth-child(4n-7) {\n    background: $orange-med;\n    border: 1px solid $orange-dark;\n\n    h3 a {\n      text-shadow: 2px 2px $orange-dark;\n    }\n  }\n\n  // 2, 6, 10, etc\n  &:nth-child(4n-6) {\n    background: $green-med;\n    border: 1px solid $green-dark;\n\n    h3 a {\n      text-shadow: 2px 2px $green-dark;\n    }\n  }\n\n  // 3, 7, 11, etc\n  &:nth-child(4n-5) {\n    background: $red-med;\n    border: 1px solid $red-dark;\n\n    h3 a {\n      text-shadow: 2px 2px $red-dark;\n    }\n  }\n\n  // 4, 8, 12, etc\n  &:nth-child(4n) {\n    background: $aqua;\n    border: 1px solid $aqua-dark;\n\n    h3 a {\n      text-shadow: 2px 2px $aqua-dark;\n    }\n  }\n}\n",".prize-card .card {\n  background-color: $ice;\n  border: 1px solid $navy;\n  border-radius: 0;\n  color: $navy;\n  height: 100%;\n  box-shadow: 5px 5px $shadow;\n\n  .card-reveal {\n    background-color: $ice;\n\n    p {\n      font-family: $font-family-sans-serif;\n      font-size: 14px;\n    }\n  }\n\n  .card-image img {\n    border-radius: 0;\n  }\n}\n\n.page-prizes-data .row .col {\n  padding: 15px;\n}\n\n.card-title {\n  font-family: $font-family-serif;\n  font-size: 18px;\n  line-height: 1;\n  text-shadow: 1px 2px $shadow;\n}\n\n.prizes .card-content {\n  border-top: 1px solid $navy;\n\n  p {\n    font-family: $font-family-sans-serif;\n    font-size: 14px;\n  }\n}\n\n@media only screen and (max-width: 768px) {\n  .page-prizes-data .row .col {\n    padding: 10px;\n  }\n\n  .card .card-content,.card .card-reveal {\n    padding: 15px;\n  }\n}\n@media only screen and (max-width: 425px) {\n  .card .card-content, .card .card-reveal {\n    padding: 25px;\n  }\n\n  .card .card-title {\n    font-size: 30px;\n  }\n\n  .prize-card .card {\n    .card-content p, .card-reveal p {\n      font-size: 16px;\n    }\n  }\n}\n","//Global mixins for seasons\n@mixin learn-more-btn {\n  box-shadow: 7px 8px $shadow;\n  color: white;\n  display: inline-block;\n  font-family: $font-comic;\n  font-size: 26px;\n  text-transform: uppercase;\n  transform: translate(0, 0);\n  transition: all 0.3s ease;\n  padding: 5px 30px;\n  position: relative;\n}\n@mixin learn-more-btn-before {\n  content: \" \";\n  position: absolute;\n  z-index: -1;\n  top: -6px;\n  left: -6px;\n  right: -6px;\n  bottom: -6px;\n}\n@mixin learn-more-btn-hover {\n  box-shadow: 2px 4px $shadow;\n  transform: translate(2px, 2px);\n}\n\n.future-events {\n  padding: 0;\n  position: relative;\n}\n\n.month {\n  color: white;\n  font-family: $font-family-sans-serif;\n  font-size: 28px;\n  font-weight: lighter;\n  text-transform: uppercase;\n}\n\n.day {\n  color: white;\n  font-family: $font-family-serif;\n  font-size: 48px;\n  font-weight: bold;\n}\n\n.save-date {\n  position: absolute;\n}\n\n.season-end {\n  position: relative;\n\n  img {\n    max-width: 100%;\n    max-height: 350px;\n  }\n}\n\n// Entomology ($aqua)\n.entomology {\n  .learn-more-btn {\n    @include learn-more-btn;\n\n    background-color: $aqua;\n    border: 6px solid $aqua-light;\n    text-shadow: 2px 4px $aqua-dark;\n\n    &::before {\n      @include learn-more-btn-before;\n\n      border: 2px solid $aqua-dark;\n    }\n\n    &:hover {\n      @include learn-more-btn-hover;\n\n      background-color: $aqua-light;\n      border: 6px solid $aqua;\n    }\n\n    &:active {\n      background-color: $aqua;\n      border: 6px solid $aqua-dark;\n      box-shadow: 0 0;\n    }\n  }\n\n  &.event {\n    background: $aqua;\n\n    .date {\n      background-color: $aqua;\n      border: 6px solid $aqua-light;\n\n      &::before {\n        border: 2px solid $aqua-dark;\n      }\n    }\n\n    .month {\n      text-shadow: 2px 4px $aqua-dark;\n    }\n\n    .day {\n      text-shadow: 2px 4px $aqua-dark;\n    }\n\n    .title {\n      border-top: 9px solid $aqua-light;\n    }\n  }\n}\n\n// Herpetology ($orange-med)\n//Ornithology ($red-med)\n//Botany ($green-med)\n","/**\n * WordPress Generated Classes\n * @see http://codex.wordpress.org/CSS#WordPress_Generated_Classes\n */\n\n/** Media alignment */\n.alignnone {\n  margin-left: 0;\n  margin-right: 0;\n  max-width: 100%;\n  height: auto;\n}\n\n.aligncenter {\n  display: block;\n  margin: ($spacer / 2) auto;\n  height: auto;\n}\n\n.alignleft, .alignright {\n  margin-bottom: ($spacer / 2);\n  height: auto;\n}\n\n@media (min-width: 30rem) {\n  .alignleft {\n    float: left;\n    margin-right: ($spacer / 2);\n  }\n\n  .alignright {\n    float: right;\n    margin-left: ($spacer / 2);\n  }\n}\n\n/** Captions */\n\n// TODO: .wp-caption {}\n// TODO: .wp-caption img {}\n// TODO: .wp-caption-text {}\n\n/** Text meant only for screen readers */\n.screen-reader-text {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  margin: -1px;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  border: 0;\n  color: #000;\n  background: #FFF;\n}\n","// Shape mixin\n@mixin triangle($side) {\n  $border-width: null;\n  $border-side: null;\n  $offset-top: 0;\n\n  @if $side == \"right\" {\n    $border-width: 85px 20px 0 0;\n    $border-side: \"border-top\";\n    $offset-top: 0;\n  }\n\n  @elseif $side == \"left\" {\n    $border-width: 0 0 85px 20px;\n    $border-side: \"border-bottom\";\n    $offset-top: -1px;\n  }\n\n  &::before, &::after {\n    content: '';\n    position: absolute;\n    top: 0;\n    width: 0;\n    height: 0;\n    border-style: solid;\n    border-width: $border-width;\n    border-color: transparent;\n  }\n\n  &::before {\n    #{$side}: -21px;\n    #{$border-side}-color: $navy;\n    z-index: -2;\n  }\n\n  &::after {\n    top: $offset-top;\n    #{$side}: -20px;\n    #{$border-side}-color: $white;\n    z-index: -1;\n  }\n}\n\n.nav-wrapper {\n  display: flex;\n  align-items: stretch;\n\n  & > * {\n    flex: 1 1 auto;\n  }\n}\n\nnav {\n  background: $ice;\n  height: 94px;\n  border-bottom: 1px solid $navy;\n  box-shadow: none;\n  padding-bottom: 8px;\n\n  .brand-